import "./style.scss"

console.log("from src/index.js")


function populateHero() {
    const heroContainer = document.getElementById('hero-section');
    // heroContainer.setAttribute("id","hero-section")

    const heroTitle = document.createElement('h1');
    heroTitle.innerText = 'Zest Fest';
    heroContainer.appendChild(heroTitle);

    const heroImg = document.createElement('img');
    heroImg.setAttribute("src", "./pizza1.png");
    heroImg.setAttribute('id', 'hero-image');
    heroContainer.appendChild(heroImg);

    const heroPara = document.createElement('p');
    heroContainer.appendChild(heroPara);
    
    const heroBtn = document.createElement('button');
    heroBtn.classList.add('cta-btn');
    heroContainer.appendChild(heroBtn)
};

populateHero();